.loadingTask--container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32,35,41,0.15);
}

.loadingText{
    margin: 24px 0 24px 24px;
    width: calc(100% -120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.icon,
.loading-deleteIcon{
    border-radius: 50px;
    height: 48px;
    width: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.4rem;
}

.icon{
    position: absolute;
    left: 12px;
}

.loading-deleteIcon{
    position: absolute;
    top: -24px;
    right: 0;
}

.loadingTask--container,
.icon,
.loading-deleteIcon{
    background: linear-gradient(90deg, rgb(0, 0, 0), rgba(200,199,199));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}